import { CONSTANTS } from "../action";

let listID = 1; 
let cardID = 6; 

const initialState = [ 
    { title: "example Episode", 
    id: `list-${0}`, 
    cards: [ 
    { id: `card-${0}`,  cards: ["card-0"], text: "we created a static list and a static card" }, 
    { id: `card-${1}`,  cards: ["card-0"], text: "we used a mix between material" } 
] 
}
];
const listsReducer = (state = initialState, action) => 
{ 
    switch (action.type) { 
        case CONSTANTS.ADD_LIST:
            const newList = {
                title:action.payload,
                cards: [],
                id: `list-${listID}`
            };
        listID += 1
        return [...state, newList];

        case CONSTANTS.ADD_CARD:{
        const newCard = {
            text: action.payload.text,
            id: `card-${cardID}`,
        }
        cardID += 1
        const newState = state.map(list => {
            if (list.id === action.payload.listID) {
                return {
                    ...list,
                    cards: [...list.cards, newCard]
                }
            } else {
                return list
            }
        })
        return newState;
    }

        case CONSTANTS.DRAG_HAPPENED:
            const {
                droppableIdStart,
                droppableIdEnd,
                droppableIndexEnd,
                droppableIndexStart
            } = action.payload;
            const newState = [...state];

            // 
            if(droppableIdStart === droppableIdEnd){
                const list = state.find(list => droppableIdStart === list.id)
                const card = list.cards.splice(droppableIndexStart, 1)
                list.cards.splice(droppableIndexEnd, 0, ...card)
            }

      //       
      if (droppableIdStart !== droppableIdEnd) {
        //   
        const listStart = state[droppableIdStart];
        //   
        const card = listStart.cards.splice(droppableIndexStart, 1);
        //   
        const listEnd = state[droppableIdEnd];

        //   
        listEnd.cards.splice(droppableIndexEnd, 0, ...card);
        return {
          ...state,
          [droppableIdStart]: listStart,
          [droppableIdEnd]: listEnd
        };
    }

            return newState;

            case CONSTANTS.EDIT_CARD: {
                const { id, listID, newText } = action.payload;
                return state.map(list => {
                  if (list.id === listID) {
                    const newCards = list.cards.map(card => {
                      if (card.id === id) {
                        card.text = newText;
                        return card;
                      }
                      return card;
                    });
                    return { ...list, cards: newCards };
                  }
                  return list;
                });
              }
            
              case CONSTANTS.DELETE_CARD: {
                const { id, listID } = action.payload;
                return state.map(list => {
                  if (list.id === listID) {
                    const newCards = list.cards.filter(card => card.id !== id);
                    return { ...list, cards: newCards };
                  } else {
                    return list;
                  }
                });
              }
          
              case CONSTANTS.EDIT_LIST_TITLE: {
                const { listID, newTitle } = action.payload;
          
                const list = state[listID];
                list.title = newTitle;
                return { ...state, [listID]: list };
              }
          
              case CONSTANTS.DELETE_LIST: {
                console.log("deletlist list reduceres");
                const { listID } = action.payload;
                console.log(state.filter(list => list.id !== listID));
                return state.filter(list => list.id !== listID);
              }
        
        default:
            return state; 

            
    } 
};

export default listsReducer;